import {Octokit} from "@octokit/rest";
import {ProxyAgent, fetch as undiciFetch} from "undici";
import {version} from "../../package.json";
import {getGitHubApiUri} from "../configuration/configuration";

export const userAgent = `VS Code GitHub Actions (${version})`;

const customFetch = (url: string, opts: any) => {
  return undiciFetch(url, {
    ...opts,
    dipatcher: new ProxyAgent({
      uri: process.env.HTTPS_PROXY || "",
      keepAliveTimeout: 10,
      keepAliveMaxTimeout: 10,
    }),
  });
};

export function getClient(token: string): Octokit {
  return new Octokit({
    auth: token,
    userAgent: userAgent,
    baseUrl: getGitHubApiUri(),
    request: {fetch: customFetch}
  });
}
